import React, { useState, useEffect, createRef } from 'react'
import { Formik } from 'formik'
import * as Yup from 'yup'

import Mathjax from 'react-mathjax'
import rk4 from 'ode-rk4'
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Legend, Tooltip } from 'recharts'

import './bootstrap.min.css'

import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Card from 'react-bootstrap/Card'

import './App.css'

function copy (x) {
  return Object.assign({}, x)
}

function simulate (f, t0, y0, step, tmax) {
  var integrator = rk4(y0, f, t0, step)
  var t = t0
  var y = y0
  var ta = []
  var ya = []
  ta.push(t0)
  ya.push(copy(y))
  while (true) {
    t = t + step
    if (t > tmax) break
    integrator = integrator.step()
    ya.push(copy(integrator.y))
    ta.push(t)
  }
  return { t: ta, y: ya }
}

function sirwrapper (params) {
  const sir = (dydt, y, t) => {
    dydt[0] = -1 * params.b * y[0] * y[1]
    dydt[1] = params.b * y[0] * y[1] - params.g * y[1]
    dydt[2] = params.g * y[1]
  }
  return sir
}

function M (eqn) {
  return <Mathjax.Node inline formula={eqn}></Mathjax.Node>
}

const initialState = {
  b: 0.36, // contact rate
  g: 0.075, // recovery rate
  I0: 0.01, // initial fraction
  step: 1,
  tmax: 60 // time window
}

function ParameterForm (props) {
  return (
    <Formik
      validationSchema={Yup.object({
        b: Yup.number().min(0).max(1).required(),
        g: Yup.number().min(0).max(1).required(),
        I0: Yup.number().min(0).max(1).required(),
        tmax: Yup.number().min(1).required()
      })}
      initialValues={initialState}
      onSubmit={props.onSubmit}
    >
      {({ values, handleChange, handleSubmit }) => (
        <Form noValidate>
          <Form.Row>
            <Form.Group as={Col} md='6' controlId='vf1'>
              <Form.Label>Initial fractions of infected people {M('I_0')}</Form.Label>
              <Form.Control
                type='number'
                name='I0'
                value={values.I0}
                min={0}
                max={1}
                step={0.01}
                onChange={handleChange}
                style={{ fontSize: '14pt' }}
              />
            </Form.Group>
            <Form.Group as={Col} md='6' controlId='vf2'>
              <Form.Label>The disease contact rate {M('\\beta')}</Form.Label>
              <Form.Control
                type='number'
                name='b'
                value={values.b}
                min={0}
                max={1}
                step={0.01}
                onChange={handleChange}
                style={{ fontSize: '14pt' }}
              />
            </Form.Group>
          </Form.Row>
          <Form.Row>
            <Form.Group as={Col} md='6' controlId='vf3'>
              <Form.Label>Recovery rate {M('\\gamma')}</Form.Label>
              <Form.Control
                type='number'
                name='g'
                value={values.g}
                min={0}
                max={1}
                step={0.001}
                onChange={handleChange}
                style={{ fontSize: '14pt' }}
              />
            </Form.Group>
            <Form.Group as={Col} md='6' controlId='vf4'>
              <Form.Label>Time window {M('T_{max}')}</Form.Label>
              <Form.Control
                type='number'
                name='tmax'
                value={values.tmax}
                min={10}
                max={365}
                step={1}
                onChange={handleChange}
                style={{ fontSize: '14pt' }}
              />
            </Form.Group>
            <Form.Group as={Col} md='12' controlId='vf5'>
              <Button
                type='button'
                className='outline'
                onClick={handleSubmit}
                style={{ fontSize: '14pt' }}
              >
                Calculate
              </Button>
            </Form.Group>
          </Form.Row>
        </Form>
      )}
    </Formik>
  )
}

const App = () => {
  const { innerWidth } = window
  const [params, setParams] = useState(initialState)
  const data = createRef()

  const calculate = () => {
    const solution = simulate(sirwrapper(params), 0, [1.0 - params.I0, params.I0, 0.0], params.step, params.tmax)

    const t = solution.t
    const S = solution.y.map((x) => { return x[0] })
    const I = solution.y.map((x) => { return x[1] })
    const R = solution.y.map((x) => { return x[2] })

    const arr = []
    for (let i = 0; i < t.length; i++) {
      arr.push({ susceptible: 100.0 * S[i], infected: 100.0 * I[i], recovered: 100.0 * R[i], name: t[i] })
    }

    return arr
  }

  if (!data.current) data.current = calculate()

  useEffect(() => {
    data.current = calculate()
  })

  const CustomTooltip = ({ active, payload, label }) => {
    if (active) {
      return (
        <Card style={{ width: '15rem' }}>
          <Card.Body style={{ fontSize: '12pt' }}>
            <Card.Title>Day {label} </Card.Title>
            <Card.Text>
              <span>Susceptible : {Math.round(100 * payload[0].value) / 100}%</span> <br />
              <span>Infected : {Math.round(100 * payload[1].value) / 100}%</span><br />
              <span>Recovered : {Math.round(100 * payload[2].value) / 100}%</span>
            </Card.Text>
          </Card.Body>
        </Card>
      )
    }

    return null
  }

  return (
    <>
      <Navbar bg='light' expand='lg'>
        <Nav inline='true' className='mr-auto'>
          <Navbar.Brand><a href='https://github.com/linerocks'>linerocks</a></Navbar.Brand>
        </Nav>
      </Navbar>
      <Container className='mt-5'>
        <h1 className='header' style={{ fontSize: '48pt' }}>The SIR Epidemic Model</h1>
        <h4 className='header mb-5'>Simple mathematical modelling of the spread of a disease</h4>
        <div style={{ fontSize: '14pt' }}>
          <Mathjax.Provider>
            <p className='mb-5'>
              Disease spread can be modelled with one of the epidemiology model so-called
          compartmental model. This model separates the population {M('N')} in to categories that change over time :
            </p>
            <ul className='mt-3'>
              <li>{M('S(t)')} are those who have not been infected, but susceptible to the disease;</li>
              <li>{M('I(t)')}  is the number of infectious individuals;</li>
              <li>{M('R(t)')}  are those individuals who have recovered from the disease and now have immunity to it.</li>
            </ul>

            The SIR model describes the change in the population of each of these compartments in terms of
            two parameters, {M('\\beta')} and {M('\\gamma')}. {M('\\beta')} describes the effective contact rate of the disease: an infected
            individual comes into contact with {M('\\beta N')} other individuals per unit time (of which the fraction
            that are susceptible to contracting the disease is {M('S/N')}). {M('\\gamma')} is the mean recovery rate: that is,
            {M('1 / \\gamma')} is the mean period of time during which an infected individual can pass it on.
            <br />              <br />
            The differential equations describing this model were first derived by Kermack and McKendrick [1]:

            <Mathjax.Node formula={(`
              \\begin{align}
                \\frac{dS}{dt} &= -\\frac{\\beta S I}{N}, \\\\
                \\frac{dI}{dt} &= \\frac{\\beta S I}{N} - \\gamma I, \\\\
                \\frac{dR}{dt} &= \\gamma I. \\\\
              \\end{align}
            `)}
            />
            <p>
              Using the differential equation above and parameters below, the dynamics can be plotted as shown.
            </p>
            <br />
            <ParameterForm onSubmit={setParams} className='my-3' />
            {innerWidth < 800
              ? (<><Button className='my-5' variant='outline-danger' disabled>Please use desktop or wider monitor resolution or desktop mode if you are in mobile, and then reload the page.</Button></>)
              : (
                <>
                  <Row className='mx-auto d-block justify-content-center mt-5 mb-3'>
                    <Col className='col-12 '>
                      <LineChart width={0.55 * innerWidth} height={0.5 * 0.55 * innerWidth} data={data.current}>
                        <Legend verticalAlign='top' height={36} align='right' />
                        <CartesianGrid stroke='#ccc' strokeDasharray='1 1' />
                        <XAxis dataKey='name' allowDataOverflow padding={{ left: 20, right: 20 }} minTickGap={100} />
                        <YAxis />
                        <Tooltip content={<CustomTooltip />} />
                        <Line type='monotone' dataKey='susceptible' stroke='#0000ff' dot={false} activeDot={{ r: 8 }} />
                        <Line type='monotone' dataKey='infected' stroke='#ff0000' dot={false} activeDot={{ r: 8 }} />
                        <Line type='monotone' dataKey='recovered' stroke='#00ff00' dot={false} activeDot={{ r: 8 }} />
                      </LineChart>
                    </Col>
                  </Row>
                </>
              )}
          </Mathjax.Provider>

          <div className='my-5'>
            <p>Reference</p>
            <ol className='mt-0'>
              <li>Kermack, W. O. and McKendrick, A. G. "A Contribution to the Mathematical Theory of Epidemics." Proc. Roy. Soc. Lond. A 115, 700-721, 1927.</li>
              <li>
                <a href='https://scipython.com/book/chapter-8-scipy/additional-examples/the-sir-epidemic-model/'>
                  https://scipython.com/book/chapter-8-scipy/additional-examples/the-sir-epidemic-model/
                </a>
              </li>
              <li>
                <a href='https://towardsdatascience.com/modelling-the-coronavirus-epidemic-spreading-in-a-city-with-python-babd14d82fa2'>
                  https://towardsdatascience.com/modelling-the-coronavirus-epidemic-spreading-in-a-city-with-python-babd14d82fa2
                </a>
              </li>
            </ol>
          </div>
        </div>
      </Container>
    </>
  )
}

export default App
